export default {
	data() {
		return {
			money: 0,
			reached: null,
			give: null,
			is_active: null,
			id: null
		}
	},
	onLoad(options) {
		// console.log(options);
		this.id = options.id;
		this.$api.auth.giftsList().then(data => {
			// console.log(data)
			this.reached = data.reached;
			this.give = data.give;
			this.is_active = data.is_active;
		})
	},
	computed: {},
	methods: {
		recharge(id) {
			console.log(id);
			let money = parseFloat(this.money);
			let type = 1;
			this.$api.auth.recharge(money, id, type).then(data => {
				console.log(data);
				this.$api.msg(data.msg);
				this.$api.wePay(data.data);
			})
		}
	}
}
